class Mutex {
  constructor() {
    this.locked = false
    this.lock = () => {
      while (this.locked) {
        // 
      }
      this.locked = true
    }
    this.unlock = () => {
      this.locked = false
    }
    this.ensure = callback => {
      this.lock()
      const value = callback()
      this.unlock()
      return value
    }
  }
}

function generateGUID() {
  var guid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
    var r = Math.random() * 16 | 0,
      v = c == 'x' ? r : (r & 0x3 | 0x8);
    return v.toString(16);
  });
  return guid;
}

